 /* Header.css */

.header-container {
  background-color: #f0f0f0;
  padding: 20px 0;
  text-align: center;
  font-family: 'Roboto', sans-serif;
}

.profile h1 {
  margin: 0;
  font-size: 24px;
  color: #333;
}

.profile h2 {
  margin: 5px 0 20px 0;
  font-size: 16px;
  color: #555;
}

.profile-img img {
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin-bottom: 10px;
}

.profile-logo img {
  width: 100%;
  max-width: 70% ;/* Ajusta el ancho máximo del logo */
  height: auto;
}
 